import { useParams, useNavigate } from "react-router-dom";
import { Flex, Text, Heading, Button, Image, Kbd } from "@chakra-ui/react";
import { useState, useEffect } from "react";
import CreatableSelect from "react-select/creatable";
import customStyles from "./customStyle";

/**
 */
function InputExtension() {
  const navigate = useNavigate();
  /* 
  * An example of an extension

  description: "New ABC 13 9370, 13.3, 5th Gen CoreA5-8250U, 8GB RAM, 256GB SSD, power UHD Graphics, OS 10 Home, OS Office A & J 2016"
  extensionName: "Practical Metal Ball"
  icon: "https://bst.icons8.com/wp-content/uploads/2021/12/MicrosoftTeams-image-7.png"
  label: "My new profile"
  mode: "normal"
  textSuggestion: (3) ['search', 'app', 'delete']
  value: "gAUYqBlhOWFvfZz6cl8jJ" 
  */
  if (window.history.state.usr === undefined) {
    navigate("/");
  }
  const extension = window.history.state.usr.extension;
  console.log(extension);

  const [possibleValues, setPossibleValues] = useState([]);
  const [InputValue, setInputValue] = useState("");
  useEffect(() => {
    let data = [];
    for (let index = 0; index < extension.textSuggestion.length; index++) {
      const element = extension.textSuggestion[index];
      data.push({ label: element, value: element });
    }
    setPossibleValues(data);
  }, []);
  let keyPress = false;
  window.addEventListener("keydown", (e) => {
    if (keyPress) {
      return;
    } else if (e.altKey && e.key === "Backspace") {
      navigate(-1);
    }
  });
  return (
    <Flex
      justify="flex-start"
      align="center"
      h="100vh"
      width={"700px"}
      marginLeft={"auto"}
      marginRight={"auto"}
      direction={"column"}
    >
      <Flex
        paddingLeft={2}
        paddingRight={2}
        align={"center"}
        borderRadius={12}
        backgroundColor={"flish.200"}
        marginLeft={"auto"}
        height={8}
        marginBottom={4}
      >
        <Image src={extension.icon} boxSize={4} borderRadius={80} />
        <Text fontSize={12} fontWeight={600}>
          {extension.label}
        </Text>
      </Flex>
      <CreatableSelect
        defaultMenuIsOpen={true}
        maxMenuHeight={400}
        controlShouldRenderValue={true}
        options={possibleValues}
        styles={customStyles}
        inputValue={InputValue}
        onInputChange={(e) => setInputValue(e)}
        onChange={(e) => {
          const query = e.label || e || "";
          window.admin.launchInstance(extension.value, query);
          navigate("/");
          window.close();
        }}
        autoFocus
        components={{ DropdownIndicator: null }}
        formatCreateLabel={(inputValue) => `Launch app with ${inputValue}`}
      />
      <Button
        size={"sm"}
        marginLeft={"auto"}
        marginTop={"auto"}
        onClick={() => navigate(-1)}
      >
        Go back with <Kbd>Alt</Kbd> {" + "} <Kbd>Backspace</Kbd>
      </Button>
    </Flex>
  );
}

export default InputExtension;
