import {
  Text,
  Heading,
  Flex,
  Image,
  Button,
  CircularProgress,
  useToast,
  Kbd
} from "@chakra-ui/react";
import Select, { OptionProps } from "react-select";
import customStyles from "./customStyle";
import { motion } from "framer-motion";
import { useTheme } from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

function App() {
  const navigate = useNavigate();
  const toast = useToast();
  const [Loading, setLoading] = useState(true);
  const [Options, setOptions] = useState();
  const { colors } = useTheme();
  useEffect(() => {
    (async () => {
      const data = await window.admin.getData();
      setOptions(data);
      setLoading(false);
    })();
  }, []);
  return (
    <Flex
      backgroundColor={"transparent"}
      justify="center"
      align="flex-start"
      h="100vh"
      width={"700px"}
      marginLeft={"auto"}
      marginRight={"auto"}
      paddingTop={12}
    >
      <Select
        components={{
          Option: AHH,
          DropdownIndicator: null,
          NoOptionsMessage: noOption,
          LoadingMessage: loadingComponent,
        }}
        defaultMenuIsOpen={true}
        onChange={(e) => extensionChoose(e, navigate, toast)}
        placeholder="Search for a profile"
        options={Options}
        isLoading={Loading}
        styles={customStyles}
        autoFocus
        customColor={colors.flish}
      ></Select>
    </Flex>
  );
}
// Typescript not needed here
/**
 * @param  {OptionProps} props
 */
const AHH = (props) => {
  return (
    <Flex
      backgroundColor={props.isFocused ? "flish.100" : "transparent"}
      /* transition={"transition: background-color 0.5s ease"} */
      align={"center"}
      borderRadius={"10px"}
      boxShadow={props.isFocused ? "0px 0px 59px rgba(0, 0, 0, 0.07)" : ""}
      p={"12px"}
      gap={2}
      overflow={"clip"}
      _hover={props.isFocused ? {} : { backgroundColor: "flish.400" }}
      onClick={() => props.setValue(props.data)}
    >
      <Image src={props.data.icon} boxSize={"24px"} borderRadius={80} />
      <Heading
        fontWeight={600}
        color={props.isFocused ? "flish.800" : "black"}
        fontSize={14}
        flexShrink={0}
      >
        {props.data.label}
      </Heading>
      <Text
        whiteSpace={"nowrap"}
        overflow={"hidden"}
        flexShrink={2}
        fontSize={"11px"}
        color={"blackAlpha.600"}
        textOverflow={"ellipsis"}
        //https://css-tricks.com/snippets/css/truncate-string-with-ellipsis/
      >
        {props.isFocused ? props.data.description : null}
      </Text >
      {props.isFocused ? (<Text fontSize={12} fontWeight={600} p={1} paddingLeft={2} paddingRight={2} borderRadius={"8px"} backgroundColor={"flish.200"} flexShrink={0} >  Select </Text>) : null}
    </Flex>
  );
};

const noOption = () => {
  return (
    <Flex
      direction={"column"}
      backgroundColor={"transparent"}
      justify="center"
      align="center"
      gap={2}
      p={12}
    >
      <Text color={"flish.600"}>We can't find this extension.</Text>
      <Button onClick={() => window.admin.openSettings()} size={"sm"} color={"flish.700"}>
        Search it on the marketplace
      </Button>
    </Flex>
  );
};

const loadingComponent = () => {
  return (
    <Flex
      direction={"column"}
      backgroundColor={"transparent"}
      justify="center"
      align="center"
      gap={4}
      p={12}
    >
      <Text fontWeight={600} color={"flish.600"}>
        Browsing your disk to find all extensions...
      </Text>
      <CircularProgress color={"flish.700"} isIndeterminate />
    </Flex>
  );
};
function extensionChoose(e, navigate, toast) {
  switch (e.mode) {
    case "normal":
      navigate("/input", { state: { extension: e } });
      break;

    case "noArgument":
      window.admin.launchInstance(e.value);
      window.location.reload()
      window.close()
      break;

    case "headless":
      navigate("/input", { state: { extension: e } });
      break;

    default:
      toast({
        title: "Error",
        description: "We can't recognize the mode of this extension",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
      break;
  }
}
export default App;
