import React from "react";
import ReactDOM from "react-dom";
import App from "./extension";
import InputExtension from "./input";
import { HashRouter, Routes, Route, useNavigate } from "react-router-dom";
import { ChakraProvider } from "@chakra-ui/provider";
import { Box } from "@chakra-ui/react";
import { Provider } from "jotai";
import flishTheme from "@flish/chakra-theme";

const DetectBlur = () => {
  const navigate = useNavigate();
  window.addEventListener("blur", () => navigate(-1));
  return null;
};

ReactDOM.render(
  <React.StrictMode>
    <ChakraProvider theme={flishTheme}>
      <Provider>
        <Box /* backgroundColor={"rgba(239, 240, 255, 0.80)"} */>
          <HashRouter>
            <DetectBlur />
            <Routes>
              <Route path="/" element={<App />} /> {/* Extension choosing */}
              <Route path="/input" element={<InputExtension />} />{" "}
              {/* Extension input */}
            </Routes>
          </HashRouter>
        </Box>
      </Provider>
    </ChakraProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
